import { HttpClient } from '@angular/common/http';
import {Component} from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import {loadStripe, Stripe} from '@stripe/stripe-js'
import { Session } from 'protractor';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-checkout-price',
  templateUrl: './price.component.html',
  styleUrls: ['./price.component.scss']
})
export class PriceCheckoutComponent{

    // DomSanitizer for safe html content.
    constructor(private _sanitizer: DomSanitizer,
                private httpClient: HttpClient,) { }

    // Pricing Carousel
    public price = [{
        type: 'Offre nol',
        image: 'assets/images/checkout/at3.svg',
        imageAlt : 'at sign',
        title: 'Abonnement par carton digital',
        price: '15',
        duration: '/ 3 mois',
        // tslint:disable-next-line:max-line-length
        feature: this._sanitizer.bypassSecurityTrustHtml('<li>Des activits  raliser en famille le week-end</li><li>Du contenu pdagogique issu de professionnels de la petite enfance</li><li>Rception du carton avec le code de validation par mail </li>'),
      }, {
        type: 'Offre nol',
        image: 'assets/images/checkout/card.svg',
        imageAlt : 'envelope with greeting card',
        title: 'Abonnement par carton papier',
        price: '20',
        duration: '/ 3 mois',
        // tslint:disable-next-line:max-line-length
        feature: this._sanitizer.bypassSecurityTrustHtml('<li>Des activits  raliser en famille le week-end</li><li>Du contenu pdagogique issu de professionnels de la petite enfance</li><li>Rception du carton papier par la Poste</li>')
      }]


    // Pricing Carousel Options
	public pricingCarousel: any ={
	    loop: false,
        margin:30,
        nav:false,
        dots:false,
        responsive:{
            0:{
                items:1,
                dots:true,
                margin:0
            // },
            // 600:{
            //     items:1,
            //     dots:true,
            //     margin:0,
            },
            768:{
                items:2,
                dots:true,
            // },
            // 992:{
            //     items:3,
            // },
            // 1000:{
            //     items:3
            }
        }
  }
  
  public async openPayment(price){
        // Create an instance of the Stripe object with your publishable API key
      const stripePromise = loadStripe(environment.my_key);
      const stripe = await stripePromise;
      let formData = new FormData();
      // const priceTitle = (<HTMLInputElement>document.getElementById('priceTitle1')).value;
      formData.append('priceTitle', price)
        
      const url = environment.server_url + '/users/payment/init';
      const headers = {}
      this.httpClient.post(url, formData, {headers}).toPromise()
          .then((data) => {
            if ( data['id'] ) {
              stripe.redirectToCheckout({
                sessionId: data['id']
              });
            } else {console.log(data['message'])};
          })
          .catch((error) => {
          console.log(error);
          });

  }

}
