import { Component, OnInit } from '@angular/core';
import { Ci18nSupport } from 'src/app/shared/models/ci18n-support';

// 3rd party
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { BreakpointObserver } from '@angular/cdk/layout';
import { Router } from '@angular/router';

@Component({
  selector: 'app-partners',
  templateUrl: './partners.component.html',
  styleUrls: ['./partners.component.scss']
})
export class PartnersComponent implements OnInit {
  public supports = [];
  public bMobile =false;

constructor(public translate: TranslateService,
            public breakpointObserver: BreakpointObserver,
            public router: Router) {

    breakpointObserver.observe('(max-width: 767px)').subscribe(result => {
      this.bMobile = result.matches;
      
      
    });
   }

  ngOnInit() {    
    this.translate.get('HOME.PARTNERS.SUPPORTS').toPromise().then((data) => {this.supports = data; console.log("data loaded..");
    })
  }


  public navigateTo(link: string) {
    this.router.navigateByUrl(link);
  }

  public partnersCarousel: any ={
    loop: false,
    margin:30,
    // nav:false,
    dots:false,
    items: 5,
    responsive:{
      0:{
          items:1,
          dots:true,
      },
      600:{
          items:1,
          dots:true,
      },
      // 768:{
      //     items:2,
      //     dots:true,
      // },
      // 992:{
      //     items:3,
      //     dots:true,
      // },
      1000:{
          items:5,
          dots:true,
      }
    }       
  }
}
