import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {IntroCheckoutComponent} from './intro/intro.component';
import {CheckoutBenefComponent} from './benefits/benefits.component';
import {PriceCheckoutComponent} from './price/price.component';

import {CheckoutRoutingModule} from './checkout-routing.module';

import { SharedModule } from '../shared/shared.module';
// 3rd party
import { OwlModule } from 'ngx-owl-carousel';
import { NgxPageScrollModule } from 'ngx-page-scroll';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { FeatureCheckoutComponent } from './feature/feature.component';
import {FAQComponent} from './FAQ/faq.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ReactiveFormsModule,
    FormsModule,
    // 3rd party
    OwlModule,
    NgxPageScrollModule,
    FontAwesomeModule,
    CheckoutRoutingModule
  ],
  declarations: [IntroCheckoutComponent, CheckoutBenefComponent, FeatureCheckoutComponent, PriceCheckoutComponent, FAQComponent],
  providers: [],
  exports: [
    IntroCheckoutComponent,
    CheckoutBenefComponent,
    FeatureCheckoutComponent,
    PriceCheckoutComponent,
    FAQComponent
  ],
  entryComponents: [IntroCheckoutComponent, CheckoutBenefComponent, FeatureCheckoutComponent, PriceCheckoutComponent, FAQComponent]
})
export class CheckoutModule { }
