import { Component, OnInit, Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import * as CryptoJS from 'crypto-js';



export interface userLogin {
  'email':string;
  'password':string; 
}

export interface userSignup {
  'email':string;
  'password':string;
  'name':string;
  'firstName':string
}

@Injectable({
  providedIn: 'root'
})

export class UserMgmtService {

	tokenKey = "gqergesthnrdtjn";
	expDateKey = "gqergesthnrdtjn_azfcz"
	token:string="";
	exp_date:string="";
	username:string="";

  constructor(
    private httpClient:HttpClient,
    private router:Router
    ) { };

  public async logIn(user:userLogin){
		const hash_pass = CryptoJS.SHA256(user.password).toString(CryptoJS.enc.Hex);
		const req = await this.httpClient.post(`${environment.server_url}/auth/session/email`, {
			'email': user.email,
			'password_hash': hash_pass,
		}).toPromise();
		
		if (req['res']){
			localStorage.setItem(this.tokenKey, req['token']);
			localStorage.setItem(this.expDateKey, req['exp_date']);
			// localStorage.setItem('email', user.email.trim());
			this.router.navigateByUrl('/account/moncompte');
			return {'res': true};
		}
		else {
			return {
				'res': false,
				'message': req['message'],
			}
		}
	};

	async signUp(user:userSignup) {

		const formData = new FormData();
		const headers = {
			'datetime': `${Date.now()}`,
		};
		const hashedPassword = CryptoJS.SHA256(user.password).toString(CryptoJS.enc.Hex);
		formData.append('gender', "");
		formData.append('firstName', user.firstName);
		formData.append('name', user.name);
		formData.append('email', user.email.trim());
		formData.append('password', hashedPassword);
		formData.append('datetime', new Date().toISOString().split('T')[0]);
		// let message: string;
		// this.language == 'fr' ? message = 'Cration de l\'utilisateur' : message = 'Creating user...';
		// const loading = await this.loadingController.create({
		// 	message: message,
		// });
		// await loading.present();
		const req = await this.httpClient.post(`${environment.server_url}/users/create`, formData, {headers}).toPromise();
		
		if (req['results'] == "GOOD"){
			localStorage.setItem(this.tokenKey, req['token']);
			localStorage.setItem(this.expDateKey, req['exp_date']);
			// localStorage.setItem('email', user.email.trim());
			this.router.navigateByUrl('/account/moncompte');
			this.sendAlert(user.email, user.firstName,  user.name);
			return {'res': true};
		}
		else {
			return {
				'res': false,
				'message': req['message'],
			}		
		}
	};
	
	sendAlert(email, prenom, nom){
		const headers = {
			"token":this.getToken()
		}
		const formData = new FormData();
		formData.append("email", email);
		formData.append("nom", prenom);
		formData.append("prenom", nom);
		
		this.httpClient.post(`${environment.server_url}/users/web/creation`, formData, {headers}).toPromise();		
	};

	getToken(){
		const token = encodeURI(localStorage.getItem(this.tokenKey))
		return(token)
	};

	cleanUser(){
		//Method used to clean the storage
		localStorage.clear();
	}
}
