import { Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

// 3rd party
import { TranslateService } from '@ngx-translate/core';
import { Ci18nBenefit } from 'src/app/shared/models/ci18n-benefit';

@Component({
  selector: 'app-benefits-home',
  templateUrl: './benefits-home.component.html',
  styleUrls: ['./benefits-home.component.scss']
})
export class BenefitsHomeComponent implements OnInit {

  public bError = false;
  public bMedium = false;

public benefitsCarousel: any ={
    loop: false,
    margin: 50,
    nav: false,
    dots: false,
    responsive: {
        0: {
            items: 1,
            dots: true,
            margin: 0
            // },
            // 600:{
            //     items:1,
            //     dots:true,
            //     margin:0,
        },
        768: {
            items: 2,
            dots: true,
            // },
            // 992:{
            //     items:3,
            // },
            // 1000:{
            //     items:3
        }
    }
}

// Benefits Carousel
public benefits: Ci18nBenefit[] = [{
  DESCRIPTION: 'Loading...',
  IMG: {
      ALT: '',
      SRC: ''
  }
}];

constructor(breakpointObserver: BreakpointObserver, public translate: TranslateService) {
    breakpointObserver.observe('(max-width: 768px)').subscribe(result => {
        this.bMedium = result.matches;
      });
 }

ngOnInit() {
  this.translate.get('CHECKOUT.BENEFITS.BENEFIT').subscribe((res: Ci18nBenefit[] | string) => {
      if (typeof(res) === 'string') {
          this.bError = true;
          this.benefits = [{
              DESCRIPTION: 'Cannot get translation',
              IMG: {
                  ALT: '',
                  SRC: ''
              }
          }];
      } else {
          this.benefits = res as Ci18nBenefit[];
      }
  });
}

}
